import { createSlice } from '@reduxjs/toolkit';
const counterSlice = createSlice({
    name: 'counter',
    initialState: 'light',
    reducers: {
        setTheme(state, { payload }) {
            state = payload;
            return state;
        },
    },
});

export const { setTheme } = counterSlice.actions;
export default counterSlice.reducer;
