import React from 'react';

import { CommandBarButton, getTheme, IButtonProps } from '@fluentui/react';

const FluentButton: React.FC<IButtonProps> = (props) => {
    const theme = getTheme();

    return (
        <CommandBarButton
            {...props}
            styles={{
                root: {
                    color: theme.palette.themeDark,
                    background: 'transparent',
                    padding: '8px 16px',
                    boxSizing: 'border-box',
                    borderBottom: '2px solid transparent',
                },
                label: {
                    fontSize: '16px',
                    fontWeight: 600,
                },
                rootHovered: {
                    color: theme.palette.themeDark,
                    background: 'transparent',
                    borderBottom: '2px solid ' + theme.palette.themeDark,
                },
            }}
        />
    );
};
export default FluentButton;
