import {
    getTheme,
    IconButton,
    Panel,
    Nav,
    INavLinkGroup,
} from '@fluentui/react';
import { navigate } from '@reach/router';
import React from 'react';
import { useTheme } from '../../utils/ThemeHook';

const MobileNav = () => {
    const [isPanelOpen, setPanelOpen] = React.useState(false);
    const theme = getTheme();
    const { themeState } = useTheme();
    const navLinkGroups: INavLinkGroup[] = [
        {
            links: [
                {
                    name: 'Home',
                    url: '/',
                },
                {
                    name: 'About us',
                    url: '/about',
                    expandAriaLabel: 'Expand About us',
                    collapseAriaLabel: 'Collapse About us',
                    links: [
                        {
                            name: 'Child link 4',
                            url: 'http://example.com',
                            target: '_blank',
                        },
                    ],
                },

                {
                    name: 'Contact',
                    url: '/contact',
                },
            ],
        },
    ];

    return (
        <div
            style={{
                height: '100%',

                alignItems: 'center',
                display: 'flex',
            }}>
            <IconButton
                iconProps={{
                    iconName: 'GlobalNavButton',
                    style: { fontSize: 20, color: theme.palette.themeDark },
                }}
                onClick={() => setPanelOpen(true)}
                styles={{
                    root: {
                        selectors: {
                            ':hover .ms-Button-icon': {
                                color: `${theme.palette.themeDarker} !important`,
                            },
                        },
                    },
                    rootHovered: {
                        backgroundColor: theme.palette.white,
                    },
                }}
            />
            <Panel
                onDismiss={() => setPanelOpen(false)}
                onOuterClick={() => setPanelOpen(false)}
                onLightDismissClick={() => setPanelOpen(false)}
                isLightDismiss={true}
                styles={{
                    main: {
                        zIndex: 999,
                        background: theme.palette.black,
                    },
                    headerText: {
                        color: theme.palette.themeDark,
                    },
                    content: {
                        color: theme.palette.themeDark,
                    },
                    contentInner: {
                        marginTop: '24px',
                    },
                    overlay: {
                        background: theme.palette.black,
                        opacity: 0.6,
                        zIndex: 5,
                        position: 'absolute',
                    },
                }}
                headerText='Navigation'
                isOpen={isPanelOpen}>
                <Nav
                    ariaLabel='Navigation'
                    onLinkClick={(e: any, item: any) => {
                        e.preventDefault();
                        navigate(item.url);
                        setPanelOpen(false);
                    }}
                    styles={(props: any) => ({
                        linkText: { zIndex: 1, color: theme.palette.themeDark },

                        chevronIcon: {
                            zIndex: 1,
                            color: theme.palette.themeDark,
                        },
                        link: {
                            background:
                                themeState === 'dark' && props.isSelected
                                    ? theme.palette.black
                                    : undefined,
                            border: 'none',
                            ':after': {
                                borderLeft:
                                    '2px solid ' + theme.palette.themeDark,
                            },
                        },
                        compositeLink: {
                            border: 'none',
                            ':hover .ms-Nav-linkText': {
                                color: theme.palette.themeDarker,
                            },
                            ':hover .ms-Nav-chevron': {
                                zIndex: 1,
                                color:
                                    theme.palette.themeDarker + ' !important',
                            },
                        },
                    })}
                    groups={navLinkGroups}
                />
            </Panel>
        </div>
    );
};

export default MobileNav;
