import { getTheme, IconButton } from '@fluentui/react';
import React from 'react';
import { useTheme } from '../utils/ThemeHook';
import { useViewport } from '../utils/useViewport';
import DesktopNav from './Header/DesktopNav';
import MobileNav from './Header/MobileNav';

const Header: React.FC<any> = () => {
    const theme = getTheme();
    const { setTheme, themeState } = useTheme(true);
    const viewport = useViewport();

    return (
        <div
            className='header'
            style={{
                height: '52px',
                padding: '0px 24px',
                background: theme.palette.black,
                borderBottom: `1px solid ${theme.palette.themeDark}`,
                display: 'flex',
                justifyContent: 'space-between',
            }}>
            {viewport === 'desktop' ? <DesktopNav /> : <MobileNav />}
            <div
                style={{
                    height: '100%',
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                }}>
                <IconButton
                    iconProps={{
                        iconName:
                            themeState === 'light' ? 'ClearNight' : 'Sunny',
                        style: {
                            color: theme.palette.themeDark,
                            fontSize: '20px',
                            marginTop:
                                themeState === 'dark' ? '2px' : undefined,
                            marginLeft:
                                themeState === 'dark' ? '6px' : undefined,
                        },
                    }}
                    styles={{
                        root: {
                            selectors: {
                                ':hover .ms-Button-icon': {
                                    color: `${theme.palette.themeDarker} !important`,
                                },
                            },
                        },
                        rootHovered: {
                            backgroundColor: theme.palette.white,
                        },
                    }}
                    title='ClearNight'
                    onClick={() => setTheme()}
                    ariaLabel='ClearNight'
                    disabled={false}
                />
            </div>
        </div>
    );
};

export default Header;
