import React from 'react';
import { getTheme, loadTheme, initializeIcons } from '@fluentui/react';
import Header from './components/Header';
import { useTheme } from './utils/ThemeHook';
import { Router } from '@reach/router';
import HomePage from './routes/HomePage';

function App() {
    initializeIcons();

    const { themeState } = useTheme();
    if (themeState === 'dark') {
        loadTheme({
            palette: {
                themePrimary: '#0078d4',
                themeLighterAlt: '#eff6fc',
                themeLighter: '#deecf9',
                themeLight: '#c7e0f4',
                themeTertiary: '#71afe5',
                themeSecondary: '#2b88d8',
                themeDarkAlt: '#106ebe',
                themeDark: '#ffffff',
                themeDarker: '#323130',
                neutralLighterAlt: '#f8f8f8',
                neutralLighter: '#f4f4f4',
                neutralLight: '#eaeaea',
                neutralQuaternaryAlt: '#dadada',
                neutralQuaternary: '#d0d0d0',
                neutralTertiaryAlt: '#c8c8c8',
                neutralTertiary: '#c2c2c2',
                neutralSecondary: '#858585',
                neutralPrimaryAlt: '#4b4b4b',
                neutralPrimary: '#333333',
                neutralDark: '#272727',
                black: '#323130',
                white: '#e0e0e0',
            },
        });
    } else {
        loadTheme({
            palette: {
                themePrimary: '#0078d4',
                themeLighterAlt: '#eff6fc',
                themeLighter: '#deecf9',
                themeLight: '#c7e0f4',
                themeTertiary: '#71afe5',
                themeSecondary: '#2b88d8',
                themeDarkAlt: '#106ebe',
                themeDark: '#005a9e',
                themeDarker: '#005a9e',
                neutralLighterAlt: '#f8f8f8',
                neutralLighter: '#f4f4f4',
                neutralLight: '#eaeaea',
                neutralQuaternaryAlt: '#dadada',
                neutralQuaternary: '#d0d0d0',
                neutralTertiaryAlt: '#c8c8c8',
                neutralTertiary: '#c2c2c2',
                neutralSecondary: '#858585',
                neutralPrimaryAlt: '#4b4b4b',
                neutralPrimary: '#333333',
                neutralDark: '#272727',
                black: '#ffffff',
                white: 'transparent',
            },
        });
    }

    const theme = getTheme();

    return (
        <div
            style={{
                width: '100%',
                height: '100%',
                background: theme.palette.black,
            }}>
            <Header />
            <div>
                <Router>
                    <HomePage
                        path={
                            process.env.NODE_ENV !== 'development'
                                ? process.env.PUBLIC_URL + '/'
                                : '/'
                        }
                    />
                </Router>
            </div>
        </div>
    );
}

export default App;
