import React from 'react';
import { getTheme, Text } from '@fluentui/react';
import { animated, useTransition } from 'react-spring';
import { useTheme } from '../../utils/ThemeHook';
import { useViewport } from '../../utils/useViewport';

const WelcomePage: React.FC<{}> = () => {
    const [animation] = React.useState(true);
    const theme = getTheme();
    const { themeState } = useTheme();
    const viewport = useViewport();

    const transitions = useTransition(animation, null, {
        from: {
            position: 'absolute',
            left: '10%',
            opacity: 0,
            top: viewport === 'desktop' ? '30%' : '15%',
        },
        enter: {
            left: viewport === 'desktop' ? '20%' : '30%',
            top: viewport === 'desktop' ? '30%' : '15%',
            opacity: 1,
        },
    });
    const secondtransitions = useTransition(animation, null, {
        from: {
            position: 'absolute',
            left: '60%',
            opacity: 0,
            top: viewport === 'desktop' ? '50%' : '30%',
        },
        enter: {
            left: viewport === 'desktop' ? '50%' : '10%',
            top: viewport === 'desktop' ? '50%' : '25%',
            opacity: 1,
        },
    });
    return (
        <>
            {transitions.map(
                ({ item, props, key }) =>
                    item && (
                        <animated.div
                            key={key}
                            style={props}
                            className='welcome-text'>
                            <Text
                                styles={{
                                    root: {
                                        color:
                                            themeState === 'light'
                                                ? theme.palette.black
                                                : theme.palette.themeDark,
                                        userSelect: 'none',
                                    },
                                }}
                                variant={
                                    viewport === 'desktop' ? 'mega' : 'xxLarge'
                                }>
                                NVO „Nasa akcija“
                            </Text>
                        </animated.div>
                    ),
            )}
            {secondtransitions.map(
                ({ item, props, key }) =>
                    item && (
                        <animated.div
                            key={key}
                            style={props}
                            className='welcome-text-description'>
                            <Text
                                styles={{
                                    root: {
                                        color:
                                            themeState === 'light'
                                                ? theme.palette.black
                                                : theme.palette.themeDark,
                                        userSelect: 'none',
                                    },
                                }}
                                variant='large'>
                                Osnovana 2012 godine kao nevladino udruzenje
                                ciji je opsti cilj razvoj svijesti gradjana kao
                                i njihovo ukljucivanje u socijalni, ekonomski,
                                ekoloski i kulturni razvoj drustva, kvaliteta
                                zivota gradjana i lokalne zajednice u cijelini.
                            </Text>
                        </animated.div>
                    ),
            )}
        </>
    );
};
export default WelcomePage;
