import React from 'react';
import WelcomePage from '../components/Home/WelcomePage';
import { getTheme } from '@fluentui/react';
import ScrollDownButton from '../shared/ScrollDownButton';
import { useTheme } from '../utils/ThemeHook';
import { useViewport } from '../utils/useViewport';

const HomePage: React.FC<any> = () => {
    const theme = getTheme();
    const { themeState } = useTheme();
    const viewport = useViewport();
    return (
        <section
            className='home section'
            style={{
                backgroundImage:
                    themeState === 'dark'
                        ? 'url(https://i2.wp.com/volimdanilovgrad.me/wp-content/uploads/2018/03/ee1c09fd9ca271941dd04fead87e0e2b.jpeg?resize=1035%2C425&ssl=1)'
                        : 'url(https://scontent-vie1-1.xx.fbcdn.net/v/t1.0-9/104469820_2638176146396714_609291864864041402_o.jpg?_nc_cat=107&ccb=2&_nc_sid=cdbe9c&_nc_ohc=J71lV_v4KV8AX9_Hgaa&_nc_oc=AQki3_Q_CaIbI5P4GRA93Xh9fim9MMUni262TrdEQfe74tuCNxybEOWC8hKibZpvKjY&_nc_ht=scontent-vie1-1.xx&oh=1fd2d42788d3139f2f3d4c1915b847d7&oe=60203680)',
            }}>
            <WelcomePage />
            <div className='backdrop'></div>
            <ScrollDownButton
                style={{
                    fontSize: viewport === 'desktop' ? 50 : 30,
                    color:
                        themeState === 'light'
                            ? theme.palette.black
                            : theme.palette.themeDark,
                }}
                className='chevrnon-scroll'
            />
        </section>
    );
};

export default HomePage;
