import React from 'react';
import FluentButton from '../../shared/FluentButton';
import { navigate } from '@reach/router';

const DesktopNav = () => {
    console.log(process.env.PUBLIC_URL);
    console.log(process.env.NODE_ENV);
    return (
        <div
            style={{
                height: '100%',

                alignItems: 'center',
                display: 'flex',
            }}>
            <FluentButton
                text='Home'
                onClick={() =>
                    process.env.NODE_ENV !== 'development'
                        ? navigate(process.env.PUBLIC_URL + '/')
                        : navigate('/')
                }
            />
            <FluentButton
                text='About us'
                onClick={() =>
                    process.env.NODE_ENV !== 'development'
                        ? navigate(process.env.PUBLIC_URL + '/about')
                        : navigate('/about')
                }
            />
            <FluentButton
                text='Contact'
                onClick={() =>
                    process.env.NODE_ENV !== 'development'
                        ? navigate(process.env.PUBLIC_URL + '/contact')
                        : navigate('/contact')
                }
            />
        </div>
    );
};

export default DesktopNav;
